import { mapGetters, mapActions } from 'vuex';
import steps from '@/component/steps.vue';
import annualInner from '@/component/annual-inner.vue';
export default {
  components: {
    steps,
    annualInner
  },
  name: 'AnnualAssess',
  data() {
    return {
      stepsArr: [
        { label: '信息确认', active: true },
        { label: '填写自评等次', active: false },
        { label: '发起考核', active: false }
      ]
    };
  },
  methods: {
    ...mapActions([]),
    changeInfo() {
      this.$router.push({
        path: '/annual-change-info'
      });
    },
    nextStep() {
      this.$router.push({
        path: '/self-rating-grade'
      });
    }
  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {
    let elem = document.querySelector('#vux_view_box_body');
    setTimeout(() => {
      elem.style.paddingBottom = '0';
    }, 10);
  }
};
